.landingContainer{
    padding: 180px 0;
    text-align: center;
    /* background-color: lightcoral; */

    background: linear-gradient(-45deg, #ecc864, #e95f94, #3b98e4, #3fe4bd);
	background-size: 400% 400%;
	animation: gradient 13s ease infinite;
}

.landingTextContainer{
    /* border: 2px solid blue; */
    display: inline-block;
}

.landingTextGreeting{
    font-size: 170px;
    animation: fadeIn 2s;
}

.landingTextSub{
    float: left;
    font-size: 27px;
    color: white;
    margin-left: 7px;
}

.landingTextBtn{
    float: left;
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    font-size: 17px;
    padding: 15px 25px;
    border-radius: 5px;
    margin: 35px 30px 0 5px;
    border: none;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@media only screen and (max-width: 600px) {

    .landingContainer{
        padding: 0 0 30px 0;
    }

    .landingTextGreeting{
        font-size: 70px;
        margin-top: -30px;
        margin-left: 20px;
        margin-right: 20px;
    }

    .landingTextSub{
        font-size: 20px;
        margin: 20px 50px;
    }

    .landingTextBtn{
        text-align: center;
        float: none;
        margin: 5px 20px;
        margin-bottom: 20px;
    }
}

@keyframes fadeIn {
    from {
      opacity: 0; 
    }
    to {
      opacity: 1;
    }
}